import { Controller, Commands } from '@ali/4ever-cangjie';
import getCanSetQuoteBlocks from '../utils/getCanSetQuoteBlocks';

export default function disableQuote(editor: Controller, next?) {
  getCanSetQuoteBlocks(editor).forEach((block) => {
    editor.command(Commands.setNodeByKey, block.key, {
      type: block.type,
      data: {
        ...block.data,
        blockquote: false,
      },
    });
  });

  if (next) {
    next();
  }

  return editor;
}
